import React from 'react';
import ReactDom from 'react-dom';
import {Tabs, TabPanel} from '../src/index.js';

ReactDom.render((
  <Tabs
    defaultActiveKey={0}
    onchange={this.handleChange}
  >
    <TabPanel name="选项卡1">
      选项卡1内容
    </TabPanel>

    <TabPanel name="选项卡2">
      选项卡2内容
    </TabPanel>
  </Tabs>
), document.getElementById('j-app'));



